<template>
  <div>
    <el-col :span="8">
      <el-card shadow="hover">
        <div slot="header">{{ index + 1 }}: 判断题</div>
        <p>{{ question.content }}</p>
        <el-radio v-model="answer" label="true">对</el-radio>
        <el-radio v-model="answer" label="false">错</el-radio>
        <el-button type="primary" @click="submitAnAnswer" round>确认</el-button>
      </el-card>
    </el-col>
    <br />
  </div>
</template>
    
    <script>
export default {
  props: {
    question: Object,
    index: Number,
  },
  data() {
    return {
      answer: "",
    };
  },
  methods: {
    submitAnAnswer() {
      this.$emit("super_submit_answer", {
        type: 5,
        index: this.index,
        questionID: this.question.questionID,
        answer: this.answer,
      });
    },
  },
};
</script>