<template>
  <div>
    <!-- 课程内容 -->
    <el-card class="box-card">
      <div>
        {{ this.chapter.content }}
      </div>
    </el-card>
    <br>
    <p>题目:</p><br>
    <!-- 题目的遍历输出 -->
    <div
      class="containerOfComponents"
      v-for="(question, index) in questions"
      :key="index"
    >
      <component
        :is="getQuestionComponent(question.type)"
        :question="question"
        :index="index"
        @super_submit_answer="saveAnswers"
      ></component>
      <br />
    </div>
    <div v-if="noticeVisible">您已完成此章节, 已关闭答案提交, 你的答题情况为:
      正确率: {{ percentage }}
    </div>
    <el-button @click="submitChapterQuestionsAnswers" :disabled="isSubmitted()"
      >提交答案</el-button
    >
  </div>
</template>
    
    <script>
import TrueFalseQuestion from "./true_false_question.vue";
import MultipleChoiceQuestion from "./multiple_choice_question.vue";
import ShortAnswerQuestion from "./short_answer_question.vue";
import axios from "axios";

export default {
  components: {
    TrueFalseQuestion,
    MultipleChoiceQuestion,
    ShortAnswerQuestion,
  },
  props: {
    chapter: Object,
  },
  data() {
    return {
      noticeVisible: false,
      studentID: localStorage.getItem("id"),
      questions: [],
      progress:{},
      answers: [],
    };
  },
  computed:{
    percentage(){
      return (this.progress.accuracy * 100).toFixed(2) + '%';
    }
  },
  methods: {
    isSubmitted(){
      console.log(this.progress)
      console.log(this.questions)
      if (this.progress === undefined) {
        // 开放提交
        return false;
      } else {
        // 关闭提交
        return true;
      }
    },
    getQuestionComponent(type) {
      switch (type) {
        case 5:
          return "TrueFalseQuestion";
        case 6:
          return "MultipleChoiceQuestion";
        case 7:
          return "ShortAnswerQuestion";

        default:
          return "div";
      }
    },
    saveAnswers(payload) {
      console.log(payload);
      let index = payload.index;
      delete payload.index;
      payload.studentID = localStorage.getItem("id");
      this.$set(this.answers, index, payload);
    },
    submitChapterQuestionsAnswers() {
      axios
        .post(
          "http://localhost:8080/SongGuo_Learning/student/checkChapterAnswers?chapterID=" +
            this.chapter.chapterID,
          this.answers,
          {
            headers: {
              Token: localStorage.getItem("token"),
            },
          }
        )
        .then((resp) => {
          console.log(resp.data);
          this.progress = resp.data.data;
          this.$notify.success({
            title: "提交成功",
            message: "您已成功提交章节答题数据, 刷新页面以查看正确率"
          })
        });
    },
  },
  mounted() {
    // 找到题目
    axios
      .get(
        "http://localhost:8080/SongGuo_Learning/teacher/getChapterQuestions?chapterID=" +
          this.chapter.chapterID,
        {
          headers: {
            Token: localStorage.getItem("token"),
          },
        }
      )
      .then((resp) => {
        console.log(resp.data);
        this.questions = resp.data.data;
        console.log(this.questions);
      });
    // 再查询章节学习记录
    axios
      .get(
        "http://localhost:8080/SongGuo_Learning/student/getChapterLearningProgress?studentID=" +
          this.studentID +
          "&chapterID=" +
          this.chapter.chapterID,
        {
          headers: {
            Token: localStorage.getItem("token"),
          },
        }
      )
      .then((resp) => {
        // 第一次做则不会有数据返回, 若做过则返回相应情况
        console.log(resp.data);
        this.progress = resp.data.data;
        console.log("give: progress");
        console.log(this.progress);
        if(this.progress != null) {
          // 打开提示
          this.noticeVisible = true;
        }
      });
  },
};
</script>
  <style scoped>
.containerOfComponents {
  display: flex;
  flex-direction: column; /* 竖向布局 */
}
</style>
    