<template>
  <div>
    <el-page-header @back="goBack" content="学习页面"> </el-page-header>
    <el-descriptions title="课程信息">
      <el-descriptions-item label="课程ID">{{
        this.course.courseID
      }}</el-descriptions-item>
      <el-descriptions-item label="课程名字">{{
        this.course.courseName
      }}</el-descriptions-item>
      <el-descriptions-item label="课程描述">{{
        this.course.description
      }}</el-descriptions-item>
    </el-descriptions>
    <div>
      <el-collapse v-model="activeChapters">
        <el-collapse-item
          v-for="(chapter, index) in chapters"
          :key="index"
          :title="chapter.chapterName"
          :name="index"
        >
          <show_chapter_info :chapter="chapter" />
        </el-collapse-item>
      </el-collapse>
    </div>
    <h1>讨论区</h1>
    <div class="discussion">
      <div v-for="(reply, index) in replies" :key="index" class="message">
        <div class="user-info">
          <span v-if="isTeacher(reply.replierID)" class="teacherName">{{
            reply.replierName
          }}</span>
          <span v-if="isStudent(reply.replierID)" class="studentName">{{
            reply.replierName
          }}</span>
          <span class="time">{{
            formatReplyDateTime(reply.replyDateTime)
          }}</span>
        </div>
        <div class="content">
          {{ reply.content }}
        </div>
      </div>
    </div>
    <el-pagination
      :current-page="currentPage1"
      :page-sizes="[5, 10, 15]"
      :page-size="pageSize1"
      layout="total, sizes, prev, pager, next, jumper"
      :total="total"
      @size-change="handleSizeChange1"
      @current-change="handlePageChange1"
    >
    </el-pagination>
    <el-form :inline="true" :model="newReply">
      <el-form-item label="评论">
        <el-input
          type="textarea"
          v-model="newReply.content"
          placeholder="输入评论"
        ></el-input>
      </el-form-item>
      <el-form-item>
        <el-button type="primary" @click="submitNewReply">发送评论</el-button>
      </el-form-item>
    </el-form>
  </div>
</template>
  
  <script>
import axios from "axios";
import show_chapter_info from "../question_for_student_learning/show_chapter_info.vue";

export default {
  components: {
    show_chapter_info,
  },
  data() {
    let course = this.$route.query;
    let courseID = course.courseID;
    return {
      activeChapters: [],
      course: course,
      courseID: courseID,

      chapters: [],
      replies: [],
      currentPage1: 1,
      pageSize1: 5,
      total1: 100,

      newReply: {
        replierID: localStorage.getItem("id"),
        replierName: localStorage.getItem("name"),
        content: "",
        replyDateTime: "",
        courseID: courseID,
      },
    };
  },
  methods: {
    goBack() {
      this.$router.push("/student_homepage_personal_info");
    },
    handleSizeChange1(val) {
      this.pageSize1 = val;
      this.getReplyData();
    },
    handlePageChange1(val) {
      this.currentPage1 = val;
      this.getReplyData();
    },
    formatReplyDateTime(timestamp) {
      const date = new Date(timestamp);
      return date.toLocaleString();
    },
    isTeacher(replierID) {
      return replierID.toString().charAt(0) === "1";
    },
    isStudent(replierID) {
      return replierID.toString().charAt(0) === "2";
    },
    getReplyData() {
      axios
        .get(
          "http://localhost:8080/SongGuo_Learning/discussion/getCourseReplies?courseID=" +
            this.courseID +
            "&pageSize=" +
            this.pageSize1 +
            "&currentPage=" +
            this.currentPage1,
          {
            headers: {
              Token: localStorage.getItem("token"),
            },
          }
        )
        .then((resp) => {
          console.log(resp.data);
          this.replies = resp.data.data;
        });
    },
    submitNewReply() {
      axios
        .post(
          "http://localhost:8080/SongGuo_Learning/discussion/addReplyToCourse",
          this.newReply,
          {
            headers: {
              Token: localStorage.getItem("token"),
            },
          }
        )
        .then((resp) => {
          console.log(resp.data);
          this.$notify.success({
            title: "发送成功",
            message: "您已成功发送一条评论"
          })
        });
    },
  },
  mounted() {
    axios
      .get(
        "http://localhost:8080/SongGuo_Learning/teacher/selectCourseChaptersByCourseID?courseID=" +
          this.courseID,
        {
          headers: {
            Token: localStorage.getItem("token"),
          },
        }
      )
      .then((resp) => {
        if (resp.data.message == "TOKEN_IS_NULL") {
          this.$router.push("/studentlogin");
          return;
        }
        console.log(resp.data);

        this.chapters = resp.data.data;
      });
    this.getReplyData();
  },
};
</script>

  <style scoped>
.discussion {
  margin-top: 20px;
}

.message {
  margin-bottom: 10px;
  border: 1px solid #ccc;
  padding: 10px;
  border-radius: 5px;
}

.user-info {
  margin-bottom: 5px;
  font-weight: bold;
}

.content {
  color: #333;
}

.teacherName {
  margin-right: 10px;
  color: aqua;
}

.studentName {
  margin-right: 10px;
  color: green;
}

.time {
  margin-right: 10px;
}
</style>