<template>
  <div>
    <el-col :span="8">
      <el-card shadow="hover">
        <div slot="header">{{ index + 1 }}: 简答题</div>
        <p>{{ question.content }}</p>
        <el-input
          type="textarea"
          placeholder="请输入内容"
          v-model="answer"
        ></el-input>
        <el-button type="primary" @click="submitAnAnswer" round>确认</el-button>
      </el-card>
    </el-col>
    <br />
  </div>
</template>
      
      <script>
export default {
  props: {
    question: Object,
    index: Number,
  },
  data() {
    return {
      answer: "",
    };
  },
  methods: {
    submitAnAnswer() {
      this.$emit("super_submit_answer", {
        type: 7,
        index: this.index,
        questionID: this.question.questionID,
        answer: this.answer,
      });
    },
  },
};
</script>